.new-publication__title {
font-weight: normal;
font-size: 14px;
line-height: 30px;
align-items: center;
text-align: center;
margin-top:49px;
margin-bottom:52px;
}

.new-publication__form-wrapper {
background-color: #F2F2F2;
padding-top: 20px;
}

.new-publication__button-option {
margin-top: 43px;
margin-bottom: 39px;
}

.new-publication__range {
margin-bottom: 39px;
}

.new-publication__button{
font-weight: bold;
font-size: 18px;
line-height: 18px;
padding: 15px;
border: 3px solid #D22856;
box-sizing: border-box;
border-radius: 30px;
background-color: transparent;
width: 100%;
}

.new-publication__button-cancel {
border: 3px solid #283645;
color: #283645;
opacity: 0.1;
margin-top: 12px;
margin-bottom: 41px;
}
